       title:"The Girl On The Train",
       author:"Paula Hawkins",
       description:"The international phenomenon novel by Paula Hawkins has been adapted into a thrilling, first-class play that you wont want to miss. Over 20 million copies of this bestselling novel have been sold worldwide and the stage adaptation is expected to enjoy similar success.",
       description2:"Whos The Girl on the Train? Rachel Watson longs for a different life and on her daily commute she finds the perfect escape via the idealistic couple she watches through the train window. Rachel looks on as the couple create the perfect illusion of happiness and love. However, this fantasy is shattered when she learns the woman shes been watching has mysteriously disappeared. Rachel finds herself a witness and even a suspect in this thrilling mystery which reveals bigger secrets than she could have ever imagined. The Girl on the Train: A commute to worldwide success This breath-baiting story began as a novel by Paula Hawkins, which went on to become an international bestseller. The Girl on the Train rose to further popularity when Dreamworks Film adapted the story into a film starring Golden Globe-winning, BAFTA-nominated actress Emily Blunt. Now the journey goes further, after Rachel Wagstaff and Duncan Abel adapted the story into a gripping play.",
